
'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { KeyRound, ShieldAlert, Edit as EditIcon, Trophy, Shield, ArrowLeftToLine } from 'lucide-react';
import { SidebarProvider, Sidebar, SidebarTrigger, SidebarHeader, SidebarContent, SidebarMenu, SidebarMenuItem, SidebarMenuButton } from '@/components/ui/sidebar';
import ManageWorks from '@/components/admin/ManageWorks';
import ManageCompetitions from '@/components/admin/ManageCompetitions';
import Link from 'next/link';

export default function AdminPage() {
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [activeView, setActiveView] = useState<'works' | 'competitions'>('works');

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (password === '123456') {
      setIsAuthenticated(true);
      setError(null);
    } else {
      setError('密码错误，请重试。');
      setPassword('');
    }
  };

  if (isAuthenticated) {
    return (
      <SidebarProvider defaultOpen>
        <div className="flex min-h-screen bg-muted/40">
          <Sidebar collapsible="icon" className="border-r hidden md:flex border-sidebar-border" side="left"> {/* Use sidebar border */}
            <SidebarHeader className="p-3 flex flex-col items-center justify-center group-data-[collapsible=icon]:justify-center">
              <div className="mb-3 w-full">
                <Button asChild variant="ghost" className="w-full justify-start group-data-[collapsible=icon]:justify-center group-data-[collapsible=icon]:w-auto group-data-[collapsible=icon]:aspect-square text-sidebar-foreground hover:bg-sidebar-accent hover:text-sidebar-accent-foreground">
                  <Link href="/">
                    <ArrowLeftToLine className="size-5" /> {/* Icon color will be inherited */}
                    <span className="group-data-[collapsible=icon]:hidden ml-2 text-sm">返回首页</span>
                  </Link>
                </Button>
              </div>
               <Shield className="size-7 text-sidebar-primary hidden group-data-[collapsible=icon]:block mb-1" aria-label="管理菜单"/> {/* Use sidebar primary color */}
               <h2 className="text-xl font-semibold text-sidebar-foreground group-data-[collapsible=icon]:hidden">管理菜单</h2> {/* Use sidebar foreground */}
            </SidebarHeader>
            <SidebarContent className="pt-4 flex-1 overflow-y-auto">
              <SidebarMenu>
                <SidebarMenuItem>
                  <SidebarMenuButton
                    onClick={() => setActiveView('works')}
                    isActive={activeView === 'works'}
                    tooltip="作品管理"
                    className="justify-start" 
                  >
                    <EditIcon className="size-5" />
                    <span className="group-data-[collapsible=icon]:hidden ml-2">作品管理</span>
                  </SidebarMenuButton>
                </SidebarMenuItem>
                <SidebarMenuItem>
                  <SidebarMenuButton
                    onClick={() => setActiveView('competitions')}
                    isActive={activeView === 'competitions'}
                    tooltip="比赛管理"
                    className="justify-start"
                  >
                    <Trophy className="size-5" />
                    <span className="group-data-[collapsible=icon]:hidden ml-2">比赛管理</span>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              </SidebarMenu>
            </SidebarContent>
          </Sidebar>

          <div className="flex flex-col flex-1">
            <header className="sticky top-0 z-30 flex h-14 items-center gap-4 border-b bg-background px-4 sm:static sm:h-auto sm:border-0 sm:bg-transparent sm:px-6 md:hidden">
              <SidebarTrigger />
              <h1 className="text-xl font-semibold flex-1 text-center text-foreground"> {/* Use main foreground */}
                {activeView === 'works' ? '作品管理' : '比赛管理'}
              </h1>
            </header>
            <main className="flex-1 p-6 md:p-8 overflow-auto bg-background"> {/* Ensure main background */}
              <h1 className="text-2xl font-semibold mb-6 hidden md:block text-foreground"> {/* Use main foreground */}
                {activeView === 'works' ? '作品管理' : '比赛管理'}
              </h1>
              {activeView === 'works' && <ManageWorks />}
              {activeView === 'competitions' && <ManageCompetitions />}
            </main>
          </div>
        </div>
      </SidebarProvider>
    );
  }

  return (
    <div className="flex items-center justify-center min-h-screen bg-muted/40">
      <Card className="w-full max-w-md shadow-xl bg-card text-card-foreground"> {/* Standard card styling */}
        <CardHeader className="text-center">
          <KeyRound className="mx-auto h-12 w-12 text-primary mb-3" />
          <CardTitle className="text-2xl font-bold">管理后台登录</CardTitle>
          <CardDescription>请输入密码以访问管理区域。</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <Input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="请输入密码"
              className="text-center"
              aria-label="密码"
            />
            {error && (
              <Alert variant="destructive" className="text-sm">
                <ShieldAlert className="h-4 w-4" />
                <AlertTitle>登录失败</AlertTitle>
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            <Button type="submit" className="w-full">
              登录
            </Button>
          </form>
        </CardContent>
         <CardFooter className="text-xs text-muted-foreground pt-6">
          <p className="text-center w-full">
            <strong>安全提示:</strong> 此处为演示目的使用客户端密码校验，
            <br />
            请勿在实际生产环境中使用此方式保护敏感数据。
          </p>
        </CardFooter>
      </Card>
    </div>
  );
}
